const LandingPage = () => {
  return (
    <div className="h-screen flex flex-col">
      {/* Top Half with Images */}
      <div className="grid grid-cols-4 h-1/2">
        <img
          src="/image1.jpg"
          alt="Image 1"
          className="w-full h-full object-cover"
        />
        <img
          src="/image2.jpg"
          alt="Image 2"
          className="w-full h-full object-cover"
        />
        <img
          src="/image3.jpg"
          alt="Image 3"
          className="w-full h-full object-cover"
        />
        <img
          src="/image4.jpg"
          alt="Image 4"
          className="w-full h-full object-cover"
        />
      </div>

      {/* Bottom Half with Black Background */}
      <div className="h-1/2 bg-black flex flex-col items-center justify-center text-center">
        <h1 className="text-4xl font-bold text-white">
          <span className="text-blue-500">S</span>
          <span className="text-cyan-500">O</span>
          <span className="text-teal-500">K</span>
          <span className="text-green-500">K</span>
          <span className="text-yellow-500">A</span>
          <span className="text-red-500">I</span>
        </h1>
        <p className="text-lg text-gray-400 mt-2">Men Made Better</p>

        {/* Buttons */}
        <div className="flex space-x-6 mt-8">
          <button className="px-8 py-2 border border-white text-white hover:bg-white hover:text-black transition duration-300">
            Login
          </button>
          <button className="px-8 py-2 border border-white text-white hover:bg-white hover:text-black transition duration-300">
            Sign In
          </button>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
